html {
  box-sizing: border-box;
  width: 100%;
  max-width: 100dvw;
  font-size: var(--rem-function);
}

body {
  position: relative;
  width: 100%;
  min-height: -webkit-fill-available;
  margin: 0;
  font-family: var(--font-family-primary);
  font-size: clamp(16px, 2.148vw, 22px);
  line-height: 130%;
  font-weight: 400;
  color: rgba(var(--cl-b), 1);
}

main {
  min-width: 0 !important;
}
main.no-padding {
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  line-height: initial;
  color: inherit;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

svg {
  fill: transparent;
}

label {
  display: inline-flex;
}

input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
}
input::placeholder {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}
input[type=number] {
  appearance: textfield;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

textarea {
  border: none;
  outline: none;
  resize: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.container {
  position: relative;
  height: 100%;
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

[data-tab-content] {
  display: none;
}
[data-tab-content].active {
  display: block;
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

.overlay {
  position: fixed;
  height: 100%;
  min-height: 100dvh;
  display: flex;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  padding: 20px !important;
  overflow-y: auto;
  background: rgba(var(--cl-b), 0.8);
  backdrop-filter: blur(5px);
  visibility: hidden;
  opacity: 0;
  transition: all 1s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.active {
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}
.overlay.active-mode {
  z-index: 100;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s, visibility 0.3s;
  pointer-events: initial;
}

[data-btn-modal] svg {
  pointer-events: none;
}

.scroll-top {
  position: fixed;
  bottom: clamp(20px, 5.208vw, 30px);
  right: clamp(20px, 5.208vw, 30px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: all 0.3s ease;
}
.scroll-top:hover {
  transform: scale(1.05);
}
